.advantages {
  padding: 66px 88px;

  &__items {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 22px;
    width: 300px;
  }

  &__item-img {
    margin-right: 24px;
    width: 40px;
    height: 40px;
  }

  &__item-title {
    margin-bottom: 3px;
    font-size: 18px;
    line-height: 1.5;
    color: $title;

    @extend %gilroy-600;
  }

  &__item-subtitle {
    @extend %gilroy-500;
  }
}